<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    return {
      formCustom: {
        title: '',
        sort: '100',
        status: '2'
      },
      ruleCustom: {
        title: [
          { required: true, message: '分组名称不能为空', trigger: 'blur' }
        ],
        status: [
          { trigger: 'blur' }
        ],
        sort: [
          {  required: true, message: '请输入排序',trigger: 'change' }
        ],
      },
      adminGroupOne: {}
    }
  },
  mounted: function() {
    const token = getToken(),
          grpid = this.$route.query.grpid || '',
          params = {"token": token, "grpid": grpid};

    if(grpid != '') this.getAdminGroupOne(params);
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          console.log(this.formCustom)
          const token = getToken(),
                grpid = this.$route.query.grpid || '',
                sort = this.formCustom.sort,
                status = this.formCustom.status*1,
                title = this.formCustom.title,
                params = {"token": token, "grpid": grpid, "sort": sort, "status": status, "title": title};
          
          if(grpid != '') {
            const isChange = this.adminGroupOne.title != params.title || this.adminGroupOne.sort != params.sort || this.adminGroupOne.status != params.status;           
            console.log(isChange, this.adminGroupOne.title != params.title , this.adminGroupOne.sort != params.sort , this.adminGroupOne.status != params.status)
            if(isChange) {
              this.setAdminGroup(params)
            } else {
              this.$Message.warning('未做任何改变');
            }
          } else {
            this.setAdminGroup(params)
          }
          
        } else {
          // this.$Notice.error({title: res.msg});
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    },
    getAdminGroupOne (params) {
      api.adminGroupOne(params).then(res => {
      if(res.code == 0) {
        this.adminGroupOne = res.data
        this.formCustom.title = res.data.title
        this.formCustom.sort = res.data.sort
        this.formCustom.status = res.data.status
      } else {
        this.$Notice.error({
          title: res.msg
        })
      }
    })
    },
    setAdminGroup (params) {
      api.adminGroupEdit(params).then(res => {
        if(res.code == 0) {
          this.$Notice.success({desc:res.msg});
          this.$router.push({path: '/admin/adminGroup'})
        } else {
          this.$Notice.error({desc:res.msg});
        }
      }).catch(err => {
        this.$Notice.error({desc:err});
      })
    }
}
}
</script>
<template>
<div>
  <h3><Icon type="ios-bonfire" size=24 />编辑菜单</h3>
  <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
    <FormItem label="分组名称" prop="title">
      <Input v-model="formCustom.title" placeholder="在此输入分组名称"></Input>
    </FormItem>
    <FormItem label="状态">
      <RadioGroup v-model="formCustom.status">
          <Radio label="1">正常</Radio>
          <Radio label="2">停用</Radio>
      </RadioGroup>
    </FormItem>
    <FormItem label="排序" prop="sort">
      <Input v-model="formCustom.sort" placeholder="在此输入排序"></Input>
    </FormItem>
    <FormItem>
      <Button type="primary" @click="handleSubmit('formCustom')">提交</Button>
      <Button @click="handleReset('formCustom')" style="margin-left: 8px">重置</Button>
    </FormItem>
  </Form>
</div>
</template>
<style lang="scss" scoped>
  .ivu-form {
		width: 28%;
	}
</style>